import { useTranslation } from "react-i18next";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { Suspense, lazy, useEffect, useState } from "react";
import styleIF from "../../InstituteFeedback.module.css";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
const FeedbackAddQuestion = lazy(() =>
  import("../../FeedbackQuestion/FeedbackAddQuestion")
);
const FeedbackQuestionMenu = lazy(() =>
  import("../../FeedbackQuestion/FeedbackQuestionMenu")
);
const StudentFeedbackDetailQuestion = ({
  ifid,
  optionCount,
  onRefetch,
  questionCount,
  questionList,
  isEvaluate,
}) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);
  const [menu, setMenu] = useState("");
  const [list, setList] = useState([]);

  useEffect(() => {
    if (questionList?.length > 0) {
      setList(questionList);
    }
  }, [questionList]);

  const onMenu = (data) => {
    setMenu(data);
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onSearchEvent = (e) => {
    if (e.target.value) {
      let dt = getQuestionSearch(questionList, e.target.value);
      setList(() => dt);
    } else {
      setList(() => questionList);
    }
  };

  return (
    <>
      <div className={style.fm_header_container}>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
          {isEvaluate ? null : (
            <CreateButton
              label={t("feedback_add_question")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                width: "10rem",
              }}
              onAction={onToggle}
            />
          )}
        </div>
      </div>

      {list?.map((que) => (
        <section
          key={`question${que?.question_sno}`}
          className={styleIF.ifq_card}
        >
          <div className={styleIF.ifq_card_inner}>
            <h6>
              <span
                style={{
                  color: "var(--color-input-label)",
                }}
              >{`${t("question")} ${que?.question_sno}: `}</span>
              {que?.question_title ?? ""}
            </h6>
            {isEvaluate ? null : (
              <img
                src={`${assestsUserFinanceUrl}/menu.svg`}
                alt="menu icon"
                onClick={() => onMenu(que)}
                title="Menu"
                style={{
                  cursor: "pointer",
                  height: "1.5rem",
                }}
              />
            )}
          </div>

          <div className={styleIF.ifqo_cantainer}>
            {que?.options?.map((opt, index) => (
              <h6 key={`option${opt?.option_sno}${index}`}>
                {opt?.option ?? ""}
              </h6>
            ))}
          </div>
        </section>
      ))}

      {questionList?.length > 0
        ? list?.length === 0 && (
            <EmptyMessage
              title="No question found related this search."
              customStyleContainer={{
                marginTop: "10rem",
              }}
            />
          )
        : questionList?.length === 0 && (
            <EmptyMessage
              title="No question created yet."
              customStyleContainer={{
                marginTop: "10rem",
              }}
            />
          )}
      <Suspense fallback={<QvipleLoader />}>
        {add && (
          <FeedbackAddQuestion
            onClose={onToggle}
            optionCount={optionCount}
            questionCount={questionCount}
            onRefetch={onRefetch}
            ifid={ifid}
          />
        )}
        {menu && (
          <FeedbackQuestionMenu
            onClose={() => setMenu("")}
            question={menu}
            onRefetch={onRefetch}
            ifid={ifid}
          />
        )}
      </Suspense>
    </>
  );
};

export default StudentFeedbackDetailQuestion;

const getQuestionSearch = (list, search) => {
  let ft = list?.filter((lt) => {
    if (
      lt?.question_title?.includes(search) ||
      lt?.question_sno?.includes(search)
    ) {
      return lt;
    } else {
      return null;
    }
  });
  return ft;
};
